/* FONTS */
@import url("https://fonts.googleapis.com/css2?family=Aldrich&family=Damion&family=Press+Start+2P&display=swap");

/* MIXINS */
@mixin activePad($box-shadow-color, $color) {
  background-color: rgb(12, 12, 12) !important;
  box-shadow: 0 0 20px 5px $box-shadow-color;
  color: $color !important;
  transform: translateY(2px);
}

@mixin glowAnimation($light-spread) {
  color: #fff;
  text-shadow: 0 0 7px rgba(160, 38, 184, 0.3), 0 0 10px #fff,
    0 0 $light-spread #fff, 0 0 42px rgba(160, 38, 184, 0.3),
    0 0 82px rgba(160, 38, 184, 0.3), 0 0 92px rgba(160, 38, 184, 0.3),
    0 0 102px rgba(160, 38, 184, 0.3), 0 0 151px rgba(160, 38, 184, 0.3);
}

/* APP SCSS */
.App {
  height: 100vh;
  background-image: linear-gradient(
      rgba(160, 38, 184, 0.05),
      rgba(0, 0, 0, 0.7)
    ),
    url("https://images.unsplash.com/photo-1583376098548-e07ca3864369?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2370&q=80");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  flex-direction: column;
  justify-content: center;

  #title {
    text-align: center;
    color: #fff;
    padding-bottom: 30px;

    @media only screen and (max-width: 700px) {
      padding-bottom: 15px;
    }

    @media only screen and (max-width: 350px) {
      padding-bottom: 25px;
    }

    &-drum {
      font-family: "Aldrich", sans-serif;
      font-size: 50px;
      letter-spacing: 1px;
      text-transform: uppercase;
      margin-bottom: -60px;
      animation: text-flicker 2s linear infinite;

      @media only screen and (max-width: 700px) {
        font-size: 30px;
        margin-bottom: -40px;
      }

      .u {
        animation: letter-flicker 2s linear infinite;
      }
    }
    &-machine {
      font-family: "Damion", cursive;
      font-size: 90px;
      text-transform: lowercase;
      text-shadow: 0 0 7px rgba(160, 38, 184, 0.3), 0 0 10px #fff, 0 0 21px #fff,
        0 0 42px rgba(160, 38, 184, 0.3), 0 0 82px rgba(160, 38, 184, 0.3),
        0 0 92px rgba(160, 38, 184, 0.3), 0 0 102px rgba(160, 38, 184, 0.3),
        0 0 151px rgba(160, 38, 184, 0.3);
      animation: text-glow 10s linear infinite;

      @media only screen and (max-width: 700px) {
        font-size: 60px;
      }
    }
  }

  #drum-machine {
    height: 40%;
    width: 50vw;
    margin: 0 auto;
    background-color: rgb(12, 12, 12);
    color: #fff;
    border-radius: 10px;
    box-shadow: 5px 50px 80px 30px rgba(160, 38, 184, 0.2);

    display: grid;
    grid-template-columns: 1fr 35%;

    @media only screen and (max-width: 1450px) {
      width: 60vw;
    }

    @media only screen and (max-width: 1000px) {
      width: 75vw;
    }

    @media only screen and (max-width: 700px) {
      height: 60%;
      grid-template-columns: 1fr;
      grid-template-rows: min-content 1fr;
    }

    @media only screen and (max-width: 450px) {
      width: 90vw;
    }

    @media only screen and (max-width: 350px) {
      height: 45%;
    }

    #display {
      order: 2;

      font-size: 10px;
      text-transform: uppercase;
      text-align: right;
      margin: 50px 50px 55px 0;

      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      @media only screen and (max-width: 1200px) {
        margin: 35px 35px 40px 0;
      }

      @media only screen and (max-width: 700px) {
        order: 1;
        margin: 20px 20px 0 20px;
        align-items: flex-end;
      }

      .now-playing {
        font-family: "Aldrich", sans-serif;
        letter-spacing: 1px;
        margin: 0 20px 10px 0;

        @media only screen and (max-width: 700px) {
          margin: 0 20px 8px 0;
        }
      }

      .audio-name {
        font-family: "Press Start 2P", cursive;
        letter-spacing: 3px;
        width: 100%;
        background-color: rgb(0, 0, 0);
        color: rgb(24, 210, 24);
        padding: 20px;

        @media only screen and (max-width: 1200px) {
          font-size: 8px;
        }

        @media only screen and (max-width: 800px) {
          font-size: 6px;
        }

        @media only screen and (max-width: 700px) {
          font-size: 8px;
          width: 65%;
        }

        @media only screen and (max-width: 450px) {
          font-size: 6px;
          padding: 15px;
        }

        @media only screen and (max-width: 350px) {
          width: 85%;
        }
      }
    }

    .pads {
      display: grid;
      grid-template-columns: repeat(3, 1fr);

      margin: 50px;

      @media only screen and (max-width: 1200px) {
        margin: 35px;
      }

      @media only screen and (max-width: 700px) {
        margin: 20px;
      }

      @media only screen and (max-width: 700px) {
        order: 2;
      }

      .drum-pad {
        font-family: "Aldrich", sans-serif;
        font-size: 14px;
        border-radius: 2px;
        border: none;
        margin: 5px;
        background-color: rgb(19, 19, 19);
        color: #fff;
        cursor: pointer;
        outline: none;

        @media only screen and (max-width: 450px) {
          font-size: 12px;
        }

        &:active {
          background-color: rgb(19, 19, 19) !important;
        }
      }

      .active-Q,
      #drum-Q:active,
      .active-W,
      #drum-W:active,
      .active-E,
      #drum-E:active {
        @include activePad(rgba(228, 96, 255, 0.8), rgb(228, 96, 255));
      }

      .active-A,
      #drum-A:active,
      .active-S,
      #drum-S:active,
      .active-D,
      #drum-D:active {
        @include activePad(rgba(54, 214, 254, 0.8), rgb(54, 214, 254));
      }

      .active-Z,
      #drum-Z:active,
      .active-X,
      #drum-X:active,
      .active-C,
      #drum-C:active {
        @include activePad(rgba(227, 28, 62, 0.8), rgba(227, 28, 62));
      }
    }
  }
}

/* Animations */

@keyframes text-glow {
  0% {
    @include glowAnimation(20px);
  }

  30% {
    @include glowAnimation(350px);
  }

  100% {
    @include glowAnimation(20px);
  }
}

@keyframes text-flicker {
  0% {
    opacity: 0.3;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  2% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  8% {
    opacity: 0.3;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  9% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  12% {
    opacity: 0.5;
    text-shadow: 0px 0px rgba(160, 38, 184, 1);
  }
  20% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  25% {
    opacity: 0.3;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  30% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  70% {
    opacity: 0.7;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  72% {
    opacity: 0.4;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  77% {
    opacity: 0.9;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  100% {
    opacity: 0.9;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
}

@keyframes letter-flicker {
  0% {
    opacity: 0.1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  2% {
    opacity: 0.1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  4% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  19% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  21% {
    opacity: 0.1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  23% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }

  80% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  83% {
    opacity: 0.4;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
  87% {
    opacity: 1;
    text-shadow: 0px 0px 29px rgba(160, 38, 184, 1);
  }
}

